'use client';

import React, { useState } from 'react';
import { useFormik } from 'formik';
import { useRouter } from 'next/navigation';
import { emailLoginSchema, mobileLoginSchema } from '@/schemas/auth';

import UnifiedInput from '../../atoms/Input';
import styles from './LoginForm.module.scss';

const LoginForm = () => {
    const [loginMethod, setLoginMethod] = useState<'email' | 'mobile'>('email');
    const router = useRouter();

    const formik = useFormik({
        initialValues: {
            email: '',
            phoneNumber: '',
            countryCode: '+90',
            password: '',
        },
        validationSchema: loginMethod === 'email' ? emailLoginSchema : mobileLoginSchema,
        onSubmit: (values) => {
            console.log('Form submitted!', values);
        },
    });

    return (
        <form onSubmit={formik.handleSubmit} className={styles.loginForm}>
            <h2>Login To Rockie</h2>

            <div className={styles.toggleButtons}>
                <button
                    type="button"
                    className={loginMethod === 'email' ? styles.active : ''}
                    onClick={() => setLoginMethod('email')}
                >
                    Email
                </button>
                <button
                    type="button"
                    className={loginMethod === 'mobile' ? styles.active : ''}
                    onClick={() => setLoginMethod('mobile')}
                >
                    Mobile
                </button>
            </div>

            <div className={styles.inputGroup}>
                <UnifiedInput
                    method={loginMethod}
                    email={formik.values.email}
                    phoneNumber={formik.values.phoneNumber}
                    countryCode={formik.values.countryCode}
                    onEmailChange={(value) => formik.setFieldValue('email', value)}
                    onPhoneNumberChange={(value) => formik.setFieldValue('phoneNumber', value)}
                    onCountryCodeChange={(value) => formik.setFieldValue('countryCode', value)}
                />
            </div>

            <input
                type="password"
                name="password"
                value={formik.values.password}
                onChange={formik.handleChange}
                placeholder="Password"
                className={styles.input}
            />

            {formik.errors.password && formik.touched.password && (
                <div className={styles.error}>{formik.errors.password}</div>
            )}

            <div className={styles.options}>
                <label>
                    <input type="checkbox" />
                    Remember me
                </label>
                <a
                    href="#"
                    onClick={(e) => {
                        e.preventDefault();
                        router.push('/forgetPassword');
                    }}
                >
                    Forgot Password?
                </a>
            </div>

            <button type="submit" className={styles.loginButton}>
                Login
            </button>

            <p className={styles.registerPrompt}>
                Not a Member?{' '}
                <a
                    href="#"
                    onClick={(e) => {
                        e.preventDefault();
                        router.push('/register');
                    }}
                >
                    Register
                </a>
            </p>
        </form>
    );
};

export default LoginForm;
